<template>
  <div class="hello">
    <div v-if="tableData.length > 0" style="margin-bottom:20px;text-align:center">
      <p>没课？这不科学</p>
      <mu-button color="primary" @click="$router.push('/addClass')" small>添加课程</mu-button>
    </div>
    <el-table
      :data="tableData"
      border
      style="width: 100%">
      <el-table-column
        fixed
        prop="date"
        label="时间/日期"
        align="center">
      </el-table-column>
      <el-table-column
        prop="monday"
        align="center"
        label="周一"
      >
      </el-table-column>
      <el-table-column
        prop="tuesday"
        align="center"
        label="周二">
      </el-table-column>
      <el-table-column
        prop="wednesday"
        align="center"
        label="周三">
      </el-table-column>
      <el-table-column
        prop="thursday"
        align="center"
        label="周四">
      </el-table-column>
      <el-table-column
        prop="friday"
        align="center"
        label="周五">
      </el-table-column>  
      <el-table-column
        prop="saturday"
        align="center"
        label="周六">
      </el-table-column>  
      <el-table-column
        prop="sunday"
        align="center"
        label="周日">
      </el-table-column>        
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'Table',
  data () {
    return {
      tableData: this.$store.state.tableData
    }
  },
  computed: {
    getTableData(){
      return this.$store.state.tableData
    }
  },
  watch: {
    getTableData: {
      handler(n, o){
        if( n !== o ) {
          console.log('新数据', n)
          this.tableData = n
        }
      },
      deep: true
    }
  },
  methods: {
    handleAddClass(){

    }
  },
  created() {

  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
h1, h2 {
  font-weight: normal;
}
.hello{
  padding-bottom: 60px;
}
</style>



