<template>
  <div>
    <mu-list>
      <mu-list-item button :ripple="false" @click="handleQuit">
        <mu-list-item-title>
          退出当前账号
        </mu-list-item-title>
        <mu-list-item-action>
          <mu-button icon>
            <mu-icon value="keyboard_arrow_right"></mu-icon>
          </mu-button>
        </mu-list-item-action>
      </mu-list-item>
    </mu-list>
  </div>
</template>

<script>
export default {
  name: 'quit',
  methods: {
    handleQuit(){
      this.$confirm('确定退出当前账号？', '提示', {
        type: 'warning'
      }).then(({ result }) => {
        if (result) {
          this.$router.push('/person')
        }
      });      
    }
  }
}
</script>

<style scoped>
.mu-list{
  background-color: #ffffff;
}
</style>


