<template>
  <div>
    <mu-container style="text-align: center">
      <mu-bottom-sheet :open.sync="bottomSheetOpen">
        <mu-slide-picker 
          :slots="addressSlots" 
          @change="addressChange" 
          :values="address"
        ></mu-slide-picker>
      </mu-bottom-sheet>
      <mu-form ref="form" :model="addClassForm">
        <mu-form-item 
          style="padding-top:12px;" 
          prop="className" 
          :rules="classNameRules"
        >
          <mu-text-field 
            icon="book" 
            placeholder="输入课程名称"
            v-model="addClassForm.className"
            prop="className"
          />
        </mu-form-item>
        <mu-form-item prop="teacherName" :rules="teacherNameRules">
          <mu-text-field 
            icon="school" 
            placeholder="输入授课老师姓名" 
            v-model="addClassForm.teacherName"
            prop="teacherName"
          />
        </mu-form-item>
        <mu-form-item prop="dayValue" :rules="dayValueRules">
          <mu-select 
            v-model="addClassForm.dayValue" 
            icon="event_note" 
            placeholder="请选择星期"
            prop="dayValue"
          >
            <mu-option 
              v-for="option in options" 
              :key="option.label" 
              :label="option.label" 
              :value="option.value"
            />
          </mu-select>
        </mu-form-item>
        <mu-form-item prop="timeValue" :rules="timeValueRules">
          <mu-text-field 
            icon="today" 
            @focus="bottomSheetOpen = true" 
            placeholder="选择上课的节数" 
            v-model="addClassForm.timeValue"
            prop="timeValue"
          />
        </mu-form-item>
        <mu-form-item>
          <mu-text-field 
            icon="near_me" 
            placeholder="选择上课地点" 
            v-model="addClassForm.classRoom" 
          />
        </mu-form-item>
        <!-- <mu-form-item> -->
          <mu-button color="primary" @click="handleAddSure">添加上课时间</mu-button>
        <!-- </mu-form-item> -->
      </mu-form>
    </mu-container>
  </div>
</template>

<script>
const address = {
  '第1节': ['第1节','第2节','第3节','第4节','第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第2节': ['第2节','第3节','第4节','第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第3节': ['第3节','第4节','第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第4节': ['第4节','第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第5节': ['第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第6节': ['第6节','第7节','第8节','第9节','第10节','第11节','第12节'],
  '第7节': ['第7节','第8节','第9节','第10节','第11节','第12节'],
  '第8节': ['第8节','第9节','第10节','第11节','第12节'],
  '第9节': ['第9节','第10节','第11节','第12节'],
  '第10节': ['第10节','第11节','第12节'],
  '第11节': ['第11节','第12节'],
  '第12节': ['第12节']

}
export default {
  name: 'addClass',
  data() {
    return {
      classNameRules: [
        { validate: (val) => !!val, message: '必须填写课程名称' }
      ],
      teacherNameRules: [
        { validate: (val) => !!val, message: '必须填写老师名称' }
      ],
      dayValueRules: [
        { validate: (val) => !!val, message: '必须选择星期' }
      ],
      timeValueRules: [
        { validate: (val) => !!val, message: '请选择上课的节数' }
      ],
      bottomSheetOpen: false,
      addressSlots: [
        {
          width: '100%',
          textAlign: 'right',
          values: Object.keys(address)
        }, {
          width: '100%',
          textAlign: 'left',
          values: ['第1节','第2节','第3节','第4节','第5节','第6节','第7节','第8节','第9节','第10节','第11节','第12节']
        }
      ],
      addressProvince: '第1节',
      addressCity: '第1节',
      address: ['第1节', '第1节'],
      options: [
        {
          value: 'monday',
          label: '周一'
        },
        {
          value: 'tuesday',
          label: '周二'
        },
        {
          value: 'wednesday',
          label: '周三'
        },
                {
          value: 'thursday',
          label: '周四'
        },
        {
          value: 'friday',
          label: '周五'
        },
        {
          value: 'saturday',
          label: '周六'
        },
        {
          value: 'sunday',
          label: '周日'
        },
      ],
      addClassForm: {
        className: '',
        teacherName: '',
        dayValue: '',
        timeValue: '',
        classRoom: ''           
      },        
    }
  },
  methods: {
    // 添加课程的按钮点击事件
    handleAddSure(){
      this.$refs.form.validate().then((result) => {
        console.log('result', result)
        if ( result ) {
          console.log('对象', this.addClassForm)
          this.$store.commit('changeData', this.addClassForm)
          this.$confirm('成功添加课表, 是否继续添加？', '提示', {
            type: 'success'
          }).then(({ result }) => {
            if (result) {
              this.addClassForm = {
                className: '',
                teacherName: '',
                dayValue: '',
                timeValue: '',
                classRoom: ''
              }
            } else {
              this.$router.push('/') 
            }
          });
        }
      })
    },
    // select 选择事件
    addressChange (value, index) {
      switch (index) {
        case 0:
          this.addressProvince = value
          const arr = address[value]
          this.addressSlots[1].values = arr
          this.addressCity = arr[0]
          break
        case 1:
          this.addressCity = value
          break
      }
      this.address = [this.addressProvince, this.addressCity]
      this.addClassForm.timeValue = this.address
    }
  },
}
</script>

<style scoped>
ul,li{
  list-style: none;
}
</style>


