import Vue from 'vue'
import Router from 'vue-router'
import Table from '@/components/table'
import addClass from '@/components/addClass'
import Person from '@/components/person'
import Quit from '@/components/quit'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Table',
      component: Table
    },
    {
      path: '/addClass',
      name: 'addClass',
      component: addClass
    },
    {
      path: '/person',
      name: 'person',
      component: Person
    },
    {
      path: '/quit',
      name: 'quit',
      component: Quit
    }
  ]
})



