<template>
  <div>
    <div>
      <mu-paper class="demo-paper" :z-depth="4">
        <mu-avatar>
          <mu-icon value="perm_identity"></mu-icon>
        </mu-avatar>
        <br />
        <mu-button color="primary" flat small @click="handleLogin">点击头像登录</mu-button>
      </mu-paper>
      <mu-list>
        <mu-sub-header>声音开启设置</mu-sub-header>
        <mu-list-item button :ripple="false" @click="events = !events">
          <mu-list-item-title>
            事件和提醒
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-switch v-model="events" readonly></mu-switch>
          </mu-list-item-action>
        </mu-list-item>
        <mu-list-item button :ripple="false">
          <mu-list-item-title>
            注意事项
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-button icon>
              <mu-icon value="info"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
        <mu-list-item button :ripple="false">
          <mu-list-item-title>
            意见与建议
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-button icon>
              <mu-icon value="keyboard_arrow_right"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
        <mu-list-item button :ripple="false" @click="$router.push('/quit')">
          <mu-list-item-title>
            设置
          </mu-list-item-title>
          <mu-list-item-action>
            <mu-button icon>
              <mu-icon value="keyboard_arrow_right"></mu-icon>
            </mu-button>
          </mu-list-item-action>
        </mu-list-item>
      </mu-list>
    </div>
  </div>
</template>

<script>
export default {
  name: 'person',
  data(){
    return {
      events: false
    }
  },
  methods: {
    handleLogin(){

    }
  },
}
</script>

<style scoped>
.mu-paper-round{
  width: 90%;
  margin: 10px auto;
  text-align: center;
}
.mu-avatar{
  margin: 5px 0
}
.mu-list{
  background-color: #ffffff;
}
</style>


